"use client"

import * as React from "react"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { MoreHorizontal, Search, Download, Key, Plus, Trash2, Copy, Loader2, Play } from "lucide-react"

export default function DashboardPage() {
  const [tokens, setTokens] = React.useState<any[]>([])
  const [loading, setLoading] = React.useState(true)
  const [generating, setGenerating] = React.useState(false)
  const [activeTab, setActiveTab] = React.useState<'tokens' | 'playground'>('tokens')

  // Playground state
  const [searchType, setSearchType] = React.useState<'dni' | 'name'>('dni')
  const [searchDni, setSearchDni] = React.useState('')
  const [searchName, setSearchName] = React.useState({ nombres: '', paterno: '', materno: '' })
  const [searchResult, setSearchResult] = React.useState<any>(null)
  const [searching, setSearching] = React.useState(false)

  const fetchTokens = async () => {
    try {
      const res = await fetch('/api/tokens')
      const data = await res.json()
      if (data.success) {
        setTokens(data.tokens)
      }
    } catch (error) {
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  React.useEffect(() => {
    fetchTokens()
  }, [])

  const generateToken = async () => {
    setGenerating(true)
    try {
      const res = await fetch('/api/tokens', { method: 'POST' })
      const data = await res.json()
      if (data.success) {
        fetchTokens()
        // Optionally show the full token in a dialog or toast
      }
    } catch (error) {
      console.error(error)
    } finally {
      setGenerating(false)
    }
  }

  const deleteToken = async (token: string) => {
    if (!confirm('¿Estás seguro de eliminar este token?')) return

    try {
      const res = await fetch(`/api/tokens/${token}`, { method: 'DELETE' })
      if (res.ok) {
        fetchTokens()
      }
    } catch (error) {
      console.error(error)
    }
  }

  const handleSearch = async () => {
    setSearching(true)
    setSearchResult(null)
    
    const payload: any = {}
    if (searchType === 'dni') {
        payload.dni = searchDni
    } else {
        payload.nombres = searchName.nombres
        payload.apellido_paterno = searchName.paterno
        payload.apellido_materno = searchName.materno
    }

    try {
        const res = await fetch('/api/buscar-dni', {
            method: 'POST',
            headers: { 
                'Content-Type': 'application/json',
                // For playground, we rely on session cookie, but we could also use a token if we wanted to test that
            },
            body: JSON.stringify(payload)
        })
        const data = await res.json()
        setSearchResult(data)
    } catch (error) {
        setSearchResult({ success: false, message: 'Error de conexión' })
    } finally {
        setSearching(false)
    }
  }

  return (
    <div className="flex flex-col space-y-6 p-8 bg-black min-h-screen text-white">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">raean API</h1>
        <div className="flex items-center gap-2">
            <Button variant="ghost" className="h-8 text-zinc-400 hover:text-white">Documentación</Button>
            <Button variant="ghost" className="h-8 text-zinc-400 hover:text-white">Soporte</Button>
        </div>
      </div>

      <div className="flex items-center space-x-4 border-b border-zinc-800 pb-4">
        <button 
            onClick={() => setActiveTab('tokens')}
            className={`rounded-full px-4 py-1 text-sm font-medium transition-colors ${activeTab === 'tokens' ? 'bg-zinc-800 text-white' : 'text-zinc-400 hover:text-white'}`}
        >
          API Tokens
        </button>
        <button 
            onClick={() => setActiveTab('playground')}
            className={`rounded-full px-4 py-1 text-sm font-medium transition-colors ${activeTab === 'playground' ? 'bg-zinc-800 text-white' : 'text-zinc-400 hover:text-white'}`}
        >
          Playground
        </button>
      </div>

      {activeTab === 'tokens' && (
        <div className="flex flex-col gap-4">
            <div className="flex items-center justify-between">
                <div className="relative flex-1 max-w-sm">
                    <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-zinc-500" />
                    <Input
                    placeholder="Buscar token..."
                    className="pl-9 border-zinc-800 bg-zinc-900/50 text-white placeholder:text-zinc-600 focus-visible:ring-zinc-700"
                    />
                </div>
                <Button 
                    onClick={generateToken} 
                    disabled={generating}
                    className="bg-white text-black hover:bg-zinc-200"
                >
                    {generating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Plus className="mr-2 h-4 w-4" />}
                    Generar Token
                </Button>
            </div>

            <div className="rounded-md border border-zinc-800 bg-black">
            <Table>
                <TableHeader>
                <TableRow className="border-zinc-800 hover:bg-transparent">
                    <TableHead className="text-zinc-500">Token</TableHead>
                    <TableHead className="text-zinc-500">Creado</TableHead>
                    <TableHead className="text-zinc-500">Uso</TableHead>
                    <TableHead className="text-zinc-500">Último uso</TableHead>
                    <TableHead className="text-right text-zinc-500">Acciones</TableHead>
                </TableRow>
                </TableHeader>
                <TableBody>
                {loading ? (
                    <TableRow>
                        <TableCell colSpan={5} className="text-center py-8 text-zinc-500">
                            Cargando tokens...
                        </TableCell>
                    </TableRow>
                ) : tokens.length === 0 ? (
                    <TableRow>
                        <TableCell colSpan={5} className="text-center py-8 text-zinc-500">
                            No tienes tokens activos.
                        </TableCell>
                    </TableRow>
                ) : (
                    tokens.map((token, i) => (
                    <TableRow key={i} className="border-zinc-800 hover:bg-zinc-900/50">
                    <TableCell className="font-mono text-zinc-300">
                        <div className="flex items-center gap-2">
                            <div className="h-8 w-8 rounded bg-zinc-900 flex items-center justify-center border border-zinc-800">
                                <Key className="h-4 w-4 text-zinc-500" />
                            </div>
                            {token.displayToken}
                        </div>
                    </TableCell>
                    <TableCell className="text-zinc-400">
                        {new Date(token.created_at).toLocaleDateString()}
                    </TableCell>
                    <TableCell>
                        <Badge variant="secondary" className="bg-zinc-900 text-zinc-400 hover:bg-zinc-800 border-zinc-800">
                            {token.usage_count} requests
                        </Badge>
                    </TableCell>
                    <TableCell className="text-zinc-400">
                        {token.last_used ? new Date(token.last_used).toLocaleString() : '-'}
                    </TableCell>
                    <TableCell className="text-right">
                        <div className="flex items-center justify-end gap-2">
                            <Button 
                                variant="ghost" 
                                size="icon" 
                                className="h-8 w-8 text-zinc-500 hover:text-white hover:bg-zinc-900"
                                onClick={() => {
                                    navigator.clipboard.writeText(token.fullToken || token.token)
                                }}
                            >
                                <Copy className="h-4 w-4" />
                            </Button>
                            <Button 
                                variant="ghost" 
                                size="icon" 
                                className="h-8 w-8 text-red-900/50 hover:text-red-500 hover:bg-red-900/20"
                                onClick={() => deleteToken(token.token)}
                            >
                                <Trash2 className="h-4 w-4" />
                            </Button>
                        </div>
                    </TableCell>
                    </TableRow>
                )))}
                </TableBody>
            </Table>
            </div>
        </div>
      )}

      {activeTab === 'playground' && (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
              <div className="space-y-6">
                  <div className="flex items-center gap-4">
                      <Button 
                        variant={searchType === 'dni' ? "default" : "outline"}
                        onClick={() => setSearchType('dni')}
                        className={searchType === 'dni' ? "bg-white text-black hover:bg-zinc-200" : "border-zinc-800 bg-transparent text-zinc-400 hover:text-white hover:bg-zinc-900"}
                      >
                          Por DNI
                      </Button>
                      <Button 
                        variant={searchType === 'name' ? "default" : "outline"}
                        onClick={() => setSearchType('name')}
                        className={searchType === 'name' ? "bg-white text-black hover:bg-zinc-200" : "border-zinc-800 bg-transparent text-zinc-400 hover:text-white hover:bg-zinc-900"}
                      >
                          Por Nombre
                      </Button>
                  </div>

                  <div className="space-y-4 p-6 border border-zinc-800 rounded-xl bg-zinc-900/20">
                      {searchType === 'dni' ? (
                          <div className="space-y-2">
                              <label className="text-sm font-medium text-zinc-400">Número de DNI</label>
                              <Input 
                                value={searchDni}
                                onChange={(e) => setSearchDni(e.target.value)}
                                placeholder="12345678"
                                className="border-zinc-800 bg-black text-white placeholder:text-zinc-700"
                              />
                          </div>
                      ) : (
                          <div className="space-y-4">
                              <div className="space-y-2">
                                  <label className="text-sm font-medium text-zinc-400">Nombres</label>
                                  <Input 
                                    value={searchName.nombres}
                                    onChange={(e) => setSearchName({...searchName, nombres: e.target.value})}
                                    placeholder="JUAN CARLOS"
                                    className="border-zinc-800 bg-black text-white placeholder:text-zinc-700"
                                  />
                              </div>
                              <div className="grid grid-cols-2 gap-4">
                                  <div className="space-y-2">
                                      <label className="text-sm font-medium text-zinc-400">Apellido Paterno</label>
                                      <Input 
                                        value={searchName.paterno}
                                        onChange={(e) => setSearchName({...searchName, paterno: e.target.value})}
                                        placeholder="PEREZ"
                                        className="border-zinc-800 bg-black text-white placeholder:text-zinc-700"
                                      />
                                  </div>
                                  <div className="space-y-2">
                                      <label className="text-sm font-medium text-zinc-400">Apellido Materno</label>
                                      <Input 
                                        value={searchName.materno}
                                        onChange={(e) => setSearchName({...searchName, materno: e.target.value})}
                                        placeholder="LOPEZ"
                                        className="border-zinc-800 bg-black text-white placeholder:text-zinc-700"
                                      />
                                  </div>
                              </div>
                          </div>
                      )}

                      <Button 
                        onClick={handleSearch}
                        disabled={searching}
                        className="w-full bg-green-600 hover:bg-green-700 text-white font-medium"
                      >
                          {searching ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Play className="mr-2 h-4 w-4" />}
                          Ejecutar Búsqueda
                      </Button>
                  </div>
              </div>

              <div className="h-full min-h-[400px] rounded-xl border border-zinc-800 bg-black p-4 font-mono text-sm overflow-auto">
                  {searchResult ? (
                      <pre className="text-zinc-300">
                          {JSON.stringify(searchResult, null, 2)}
                      </pre>
                  ) : (
                      <div className="flex h-full items-center justify-center text-zinc-600">
                          Los resultados aparecerán aquí...
                      </div>
                  )}
              </div>
          </div>
      )}
    </div>
  )
}
